* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #333;
}

.nav {
    background-color: #333;
    color: white;
    display: flex;
    justify-content: space-between;
    height: 5rem;
    align-items: stretch;
    gap: 2rem;
    padding: 3rem 7rem;
}

.site-title {
    font-size: 3rem;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
    font-size: 1.5rem;
}

.nav a {
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
    color: inherit;
    text-decoration: none;
}

.nav li.active {
    background-color: #555;
}

.nav li:hover {
    background-color: #777;
}

.mainBody {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    flex-direction: column;
}

.textBody {
    padding-left: 25%;
    padding-right: 25%;
    padding-bottom: 5%;
    font-size: larger;
}


.form{
    display: flex;
    flex-direction: column;
    
}

.form-inputs {
    width:100%;
    padding-right: 15px;
    padding-bottom: 15px;
}
.form-inputs p {
    color: red;
}
.name-forms {
    display: flex;
    flex-direction: row;
    width: 100%;    
}
.name-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 10px;
    height: 40px;
    width: 100%;
    border:solid;
    border-color:white;
    border-width:thin;
    background-color: #333;
}

.email-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 10px;
    height: 40px;
    width: 100%;
    border:solid;
    border-color:white;
    border-width:thin;
    background-color: #333;

}



.form-content-right {
    width: 50%;
    
    
}

.form-input-btn {
    width: 40%;
    height: 50px;
    margin-top: 10px;
    border-radius: 10px;
    outline: none;
    border: none;
    color: white;
    font-size: 1rem;
  }
.button_text{
    color:#333;
}

.success_message{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 50px;
}